<script>
  import CausesOfDeathMini from "./CausesOfDeathMini.svelte";
  import MapLegend from "./MapLegend.svelte";

  import { debounce, throttle } from "./../utils.js";
  import { timer as d3Timer } from "d3-timer";

  const numberOfYears = 79;

  let activeYearIndex = 0;
  let currentTime = 0;
  let duration = 100;
  let videoElement;

  $: progress = currentTime / duration;
  $: activeYearIndex = Math.max(
    0,
    Math.min(numberOfYears, Math.round(numberOfYears * progress))
  );

  let hasSetRate = false;
  const onTimeUpdate = e => {
    if (!videoElement) return;

    if (!hasSetRate) {
      videoElement.playbackRate = 0.3;
      hasSetRate = true;
    }

    if (progress >= 0.98) {
      videoElement.pause();
      activeYearIndex = numberOfYears;
      setTimeout(() => {
        if (!videoElement) return;
        videoElement.play();
      }, 6000);
    }
  };
  $: currentTime, onTimeUpdate();
</script>

<div class="c">
  <div class="controls">
    <div class="slider">
      <div
        class="year"
        style="{`top: ${(activeYearIndex * 100) / numberOfYears}%`}">
        {activeYearIndex + 2020}
      </div>
      <!-- <input
          type="range"
          bind:value="{activeYearIndex}"
          min="0"
          max="{numberOfYears}" /> -->
    </div>
    <MapLegend {activeYearIndex} />
    <div class="bar-chart">
      <CausesOfDeathMini year="{activeYearIndex + 2020}" />
    </div>
  </div>

  <div class="video">
    <video
      bind:this="{videoElement}"
      loop
      autoplay
      muted
      bind:duration
      bind:currentTime
      playbackRate="{0.3}">
      <!-- <source src="/media/examples/flower.webm"
            type="video/webm"> -->
      <source src="/assets/map/map-1.mp4" type="video/mp4" />
      Sorry, your browser doesn't support embedded videos.
    </video>
  </div>
</div>

