<script>
  import { calculator as copy } from "./../content/data.json";
  import stateAbbreviationsToNames from "./../data/stateAbbreviationsToNames.json";

  import Number from "./Number.svelte";
  import Toggle from "./Toggle.svelte";
  import Text from "./Text.svelte";
  import Minimap from "./Minimap.svelte";
  import CeIcon from "./CeIcon.svelte";
  import NzIcon from "./NzIcon.svelte";
  import ShareImage from "./ShareImage.svelte";
  import SearchableInput from "./SearchableInput.svelte";
  import { getNumberWithUnits } from "./../utils";

  import data from "./../data/sample_data.csv";
  console.log(data);

  const dataWithId = data.map(d => ({
    ...d,
    id: [d["NAME"], d["STATE_ABBREV"]].join(", ")
  }));
  let statsByName = {};
  dataWithId.forEach(d => {
    statsByName[d.id] = d;
  });
  const regions = dataWithId.map(d => d.id);

  let selectedRegion = regions[0];
  const targetOptions = [
    {
      id: "CE",
      label: copy["clean_energy_label"],
      description: copy["clean_energy_description"],
      icon: CeIcon
    },
    {
      id: "NZ",
      label: copy["net-zero_emissions_label"],
      description: copy["net-zero_emissions_description"],
      icon: NzIcon
    }
  ];
  let selectedTarget = targetOptions[0].id;
  $: selectedTargetLabel = targetOptions
    .find(d => d.id == selectedTarget)
    .label.toLowerCase();
  $: selectedTargetDescription =
    copy[
      selectedTarget == "CE"
        ? "clean_energy_description"
        : "net-zero_emissions_description"
    ];

  $: usStats = statsByName["United States, US"];
  $: selectedRegionStats = statsByName[selectedRegion] || {};
  $: stateStats =
    statsByName[
      `${
        stateAbbreviationsToNames[(selectedRegionStats || {})["STATE_ABBREV"]]
      }, ${(selectedRegionStats || {})["STATE_ABBREV"]}`
    ] || {};
  $: selectedRegionLocation = [
    +selectedRegionStats["LON"],
    +selectedRegionStats["LAT"]
  ];

  $: sections = [
    selectedRegionStats && {
      name: selectedRegion,
      lives: selectedRegionStats[`${selectedTarget}_LIVES`],
      cost: selectedRegionStats[`${selectedTarget}_COST`]
    },
    stateStats && {
      name: stateStats["NAME"],
      lives: stateStats[`${selectedTarget}_LIVES`],
      cost: stateStats[`${selectedTarget}_COST`]
    },
    usStats && {
      name: "the United States",
      lives: usStats[`${selectedTarget}_LIVES`],
      cost: usStats[`${selectedTarget}_COST`]
    }
  ].filter(d => d);
</script>

<figure class="c">
  <div class="left">
    <div class="search">
      <SearchableInput
        bind:value="{selectedRegion}"
        placeholder="Search for a city, county, or state"
        options="{regions}"
        emptyNote="No regions found" />
    </div>

    <div class="minimap">
      <Minimap location="{selectedRegionLocation}" />
    </div>

    {#if selectedRegionStats}
      <h6>
        {@html copy.toggle_text}
      </h6>
      <!-- <Toggle options="{targetOptions}" bind:value="{selectedTarget}" /> -->
      <div class="options">
        {#each targetOptions as { id, label, description, icon }}
          <button
            class="option"
            class:selected="{id == selectedTarget}"
            on:click="{() => (selectedTarget = id)}">
            <div class="icon">
              <svelte:component this="{icon}" />
            </div>
            <div class="option-title">{label}</div>
            <div class="description">
              <Text text="{description}" />
            </div>
          </button>
        {/each}
      </div>
    {/if}
  </div>

  <div class="right">
    {#each sections as { name, label, lives, cost }}
      <div class="section">
        <div class="share-button">
          <ShareImage
            {...{ name, label, lives, cost, selectedTarget, selectedTargetLabel }} />
        </div>
        If
        <strong>{name}</strong>
        achieves
        <span class="{`highlight highlight-${selectedTarget}`}">
          {selectedTargetLabel}
        </span>
        by 2050, we would save
        <div class="stat">
          <Number number="{lives}" format="{getNumberWithUnits}" />
          {Math.round(lives) == 1 ? 'life' : 'lives'}
        </div>
        and avoid
        <div class="stat">
          $
          <Number number="{cost}" format="{getNumberWithUnits}" />
        </div>
        in adaptation costs globally by 2100.
      </div>
    {/each}
  </div>
</figure>

