<script>
  import { scaleLinear } from "d3-scale";

  import Number from "./Number.svelte";
  import regionMeans from "./../data/means.json";
  import { map_legend_title, map_legend_subhead } from "./../content/data.json";

  export let activeYearIndex = 0;

  const values = Object.values(regionMeans);

  const maxValue = Math.max(
    Math.abs(Math.min(...values)),
    Math.abs(Math.max(...values))
  );
  $: colorScale = scaleLinear(
    // ["#80c7f3", "#0b0a25", "#CC2600"]
    // ["#4477c0", "#2b2a48", "#FF6553"]
    // ["#85faff", "#4477c0", "#2b2a48", "#FF6553", "#ffb192"]
    ["#67a0f2", "#4b4a68", "#ff3720"]
  ).domain([-maxValue, 0, maxValue]);
  $: ticks = colorScale.ticks(20).map(d => [d, colorScale(d)]);
</script>

<div class="c">
  <div class="title">{map_legend_title}</div>
  <div class="subheading">{map_legend_subhead}</div>
  <div
    class="gradient"
    style="{`background: linear-gradient(to right, ${ticks
      .map(([v, color]) => color)
      .join(', ')})`}">
    <div class="dots">
      <div class="dot"></div>
      <div class="dot"></div>
      <div class="dot"></div>
    </div>
  </div>
  <div class="values">
    <div class="value">
      <Number number="{-maxValue}" format=",.0f" />

    </div>
    <div class="value">0</div>
    <div class="value">
      +
      <Number number="{maxValue}" format=",.0f" />
    </div>
  </div>
  <!-- {#each ticks as tick}{tick}{/each} -->
</div>

